<template>
  <div>
    <!--轮播图-->
    <van-swipe :autoplay="2000" indicator-color="#282828">
      <van-swipe-item v-for="(item, index) in swipeImages" :key="index">
        <van-image fit="contain" width="100%" height="10.714rem" :src="item" />
      </van-swipe-item>
    </van-swipe>

    <!--导航栏-->
    <van-row type="flex" justify="space-around" class="nav-box">
      <van-col
        v-for="(item, inx) of navList"
        class="tx-center dis-flex flex-column align-item-center"
        :key="inx"
        :span="5"
      >
        <div class="nav-icon-box"></div>
        <div class="color-282828 font-12">{{item.title}}</div>
      </van-col>
    </van-row>
    <p class="divider" />

    <!--滚动公告-->
    <div class="notice-box">
      <div class="notice-title">
        滚动
        <br />公告
      </div>

      <div class="color-656565 font-10">
        <div>
          <span class="badge">当前活动</span>
          本周六进行夏令营霍活动，欢迎报名参加。
        </div>
        <div style="margin-top: 0.5rem;">
          <span class="badge">牦牛动态提醒</span>
          来福 今天吃了 5KG 鲜草...
        </div>
      </div>
    </div>
    <p class="divider" />

    <!--牧场管理-->
    
      <p class="title color-656565 font-12">我的屠宰厂（小金屠宰厂）</p>
      <div class="dis-flex size flex-column">
        <filter-nav-control
          :filter-nav-list="ButtonList"
          v-model="activeNav"
          @change="adoptTypeChange"
        />

        <div class="flex-1" style="overflow-y: auto">
          <!--牦牛认养 Item-->
          <yak-adopt-item :is-show-adopt-bt="false" @detail="adoptDetail" />
        </div>
      </div>
  </div>
</template>

<script>
import YakAdoptItem from "../../components/yak-adopt/yak-adopt-item";
import FilterNavControl from "../../components/common/filter-nav-control";
export default {
  name: "nomads-home",
  components: { FilterNavControl, YakAdoptItem },
  data() {
    return {
      swipeImages: [
        "https://img.yzcdn.cn/vant/apple-2.jpg",
        "https://img.yzcdn.cn/vant/apple-3.jpg"
      ],
      navList: [
        { title: "商城", route: "" },
        { title: "商城", route: "" },
        { title: "商城", route: "" },
        { title: "商城", route: "" }
      ],
      ButtonList: [
        { title: "待屠宰", key: "stay" },
        { title: "屠宰中", key: "ongoing" },
        { title: "已屠宰完毕", key: "completion" },
      ],
      activeNav: "stay"
    };
  },
  methods: {
    /**
     * @description: 认养类型改变
     * @date 2020/2/12
     */
    adoptTypeChange(event) {
      if (this.activeNav !== event) this.activeNav = event;
    },
    /**
     * @description: 牦牛详情
     * @date 2020/2/12
     */
    adoptDetail(event) {
      this.$router.push({
        path: "/yak-adopt/yak-detail",
        query: {
          id: event.id,
          type: this.isActive
        }
      });
    }
  }
};
</script>

<style scoped lang="less">
