.divider {
  height: 0.214rem;
}

/deep/ .van-swipe__indicator {
  background-color: #989898;
}

/*滚动公告*/
.nav-box {
  margin-top: 1.286rem;
  padding-bottom: 1.5rem;

  /*导航栏图标容器*/

  .nav-icon-box {
    width: 2.857rem;
    height: 2.857rem;
    background: rgba(254, 165, 3, 1);
    border-radius: 50%;
  }
}

/*公告容器*/
.notice-box {
  display: flex;
  align-items: center;
  padding: 1.143rem 1.071rem;

  /*标题*/

  .notice-title {
    font-size: 0.786rem;
    font-weight: bold;
    color: rgba(251, 110, 110, 1);
    margin-right: 1.071rem;
  }
}

/*卡片盒子*/
.card-box {
